import Vue from "vue";
import Router from "vue-router";
import Index from "./views/Index.vue";
import Description from "./views/Description.vue";
import Price from "./views/Price.vue";
import Download from "./views/Download.vue";
import Other from "./views/Other.vue";
import MainNavbar from "./layout/MainNavbar.vue";
import MainFooter from "./layout/MainFooter.vue";

Vue.use(Router);

export default new Router({
  mode: 'history',
  routes: [
    {
      path: "/",
      name: "home",
      components: { default: Index, header: MainNavbar, footer: MainFooter },
      props: {
        header: { colorOnScroll: 400, type: "black" },
        footer: { backgroundColor: "black" }
      }
    },
    {
      path: "/home",
      name: "index",
      components: { default: Index, header: MainNavbar, footer: MainFooter },
      props: {
        header: { colorOnScroll: 400, type: "black" },
        footer: { backgroundColor: "black" }
      }
    },
    {
      path: "/description",
      name: "description",
      components: { default: Description, header: MainNavbar, footer: MainFooter },
      props: {
        header: { colorOnScroll: 400, type: "black" },
        footer: { backgroundColor: "black" }
      }
    },
    {
      path: "/price",
      name: "price",
      components: { default: Price, header: MainNavbar, footer: MainFooter },
      props: {
        header: { colorOnScroll: 400, type: "black"  },
        footer: { backgroundColor: "black" }
      }
    },
    {
      path: "/download",
      name: "download",
      components: { default: Download, header: MainNavbar, footer: MainFooter },
      props: {
        header: { colorOnScroll: 400 , type: "black" },
        footer: { backgroundColor: "black" }
      }
    },
    {
      path: "/other/:name",
      name: "other",
      components: { default: Other, header: MainNavbar, footer: MainFooter },
      props: {
        header: { colorOnScroll: 400, type: "black"  },
        footer: { backgroundColor: "black" }
      }
    }
  ],
  scrollBehavior: to => {
    if (to.hash) {
      return { selector: to.hash };
    } else {
      return { x: 0, y: 0 };
    }
  }
});
