import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import VueI18n from 'vue-i18n'
// 移动端适配
import './initial/flex';

import MaterialKit from "./plugins/material-kit";

Vue.config.productionTip = false;

Vue.use(MaterialKit);
Vue.use(VueI18n)
const languageSetting = navigator.systemLanguage?navigator.systemLanguage:navigator.language
let languageMine = 'en'
console.log(languageSetting)
if (languageSetting == 'zh') {
  languageMine = 'zh-CN'
} else {
  languageMine = 'en-US'
}


const i18n = new VueI18n({
  locale: languageMine,    // 语言标识
  // locale: 'en-US',    // 语言标识
  //this.$i18n.locale // 通过切换locale的值来实现语言切换
  messages: {
    'zh-CN': require('./lang/zh'),   // 中文语言包
    'en-US': require('./lang/en')    // 英文语言包
  },
  dateTimeFormats: require('./lang/date')
})

const NavbarStore = {
  showNavbar: false
};

Vue.mixin({
  data() {
    return {
      NavbarStore
    };
  }
});

new Vue({
  router,
  i18n,
  render: h => h(App)
}).$mount("#app");
